import React, { Component } from 'react';

import profileImage from '../assets/images/profile.png';

class AboutMe extends Component {

    dateDiffInYears() {
        let birthDate = new Date(1990,4,16);
        let today = new Date();

        let ynew = today.getFullYear();
        let mnew = today.getMonth();
        let dnew = today.getDate();
        let yold = birthDate.getFullYear();
        let mold = birthDate.getMonth();
        let dold = birthDate.getDate();
        let diff = ynew - yold;
        if (mold > mnew) diff--;
        else {
            if (mold === mnew) {
                if (dold > dnew) diff--;
            }
        }
        return diff;
    }

    render() {
        return (
            <div className="container about-me" id="aboutMe">
                <div className="row">
                    <div className="col-12 text-center about-me-title">
                        <h1>About me</h1>
                    </div>
                    <div className="col-12 col-lg-4">
                        <p className="text-justify">
                            Ambitious problem solver with a passion for Web Develop,who would like to join a team of like
                            - minded developers. Experience of creating logicals and mind-sided solutions to problems. <br/>
                            Continuously evaluating and upgrading my skills so that make me stay at the cutting edge of
                            web development. Natural problem solver, with the experience of successfully completing projects
                            for IT consultancies, software houses, web design agencies, and IT departments.
                        </p>
                    </div>
                    <div className="col-12 col-lg-4">
                        <img src={profileImage} className="rounded mx-auto d-block rounded-circle" alt="..." />
                    </div>
                    <div className="col-12 col-lg-4 personal-information">
                        <dl>
                            <dt>Name</dt>
                            <dd>Jesús Alberto González</dd>
                            <dt>Age</dt>
                            <dd>{this.dateDiffInYears()} years</dd>
                            <dt>Location</dt>
                            <dd>Buenos Aires. Argentina</dd>
                        </dl>
                    </div>
                </div>
            </div>
        );
    }
}

export default AboutMe;
