import React, { Component } from 'react';

class Navigation extends Component {

    scrollTo(elementId){
        let elm = document.getElementById(elementId);
        window.scrollTo(0, elm.offsetTop);
    }

    render() {
        return (
            <nav id="sidebar-wrapper">
                <ul className="sidebar-nav">
                    <li className="sidebar-nav-item">
                        <span className="js-scroll-trigger" onClick={() => this.scrollTo('home')}>Home</span>
                    </li>
                    <li className="sidebar-nav-item">
                        <span className="js-scroll-trigger" onClick={() => this.scrollTo('aboutMe')}>About</span>
                    </li>
                    <li className="sidebar-nav-item">
                        <span className="js-scroll-trigger" onClick={() => this.scrollTo('portfolio')}>Portfolio</span>
                    </li>
                    <li className="sidebar-nav-item">
                        <span className="js-scroll-trigger" onClick={() => this.scrollTo('timeline')}>Formation</span>
                    </li>
                    <li className="sidebar-nav-item">
                        <span className="js-scroll-trigger" onClick={() => this.scrollTo('skills')}>Skills</span>
                    </li>
                    <li className="sidebar-nav-item">
                        <span className="js-scroll-trigger" onClick={() => this.scrollTo('contact')}>Contact</span>
                    </li>
                </ul>
            </nav>
        );
    }
}

export default Navigation;
